import React from 'react'

const Qualification = () => {

    let qualification = [
        {
            id:1,
            year: 2013,
            degree:'Ph.D'
        },
        {
            id:2,
            year: 2009,
            degree:'Masters'
        },
        {
            id:3,
            year: 2006,
            degree:'Bechlors'
        }
    ];

  return (
    <>
      <div className="qualification row " id='Qualification'>
      {qualification.map((user,ind) => (
        <div key={ind} className="col-md-4">
            <div>{user.year}</div>
            <h3>{user.degree}</h3>
            <p>Science And Technology KIU, Gilgit.</p>
        </div>
      ))}

        
        
      </div>
    </>
  )
}

export default Qualification;
