import React, { useState } from 'react';


const Contect = () => {

  let initialValues = {
    name : "",
    email: "",
    message : ""
  }

  const [contact , setContact]=useState(initialValues)

  const contactChange= (e)=>{

    let {name,value}=e.target

    const nextFormState = {
      ...contact,
      [name]: value,
    }

    setContact(nextFormState)

    console.log(contact)
  }

  let onFormSubmit=(e)=>{

    console.log(contact)
    
  }

  return (
    <>
      <form action="">
        <div className="row w-100" id='contact'>
          <div className='form'>
            <h3 className='text-white'>Contact Me</h3>
            <input type="text" placeholder='Name' value={contact.name} name="name" onChange={contactChange}/>
            <input type="email" placeholder='Email'  name="email" onChange={contactChange}/>
            <textarea  cols="2" rows="4" placeholder='Messege'  name="message" onChange={contactChange}></textarea>
            <button className="contact" onClick={onFormSubmit}>SUBMIT</button>
          </div>
            
        </div>
      </form>
    </>
  )
}

export default Contect;
