import React from "react";
import Image from "./Images/Image2.png";

const Home = () => {
  return (
    <>
      <div className="mb-3 bg-transparent p-5 " id="home">
        <div className="row g-0 ">
          <div className="col-md-8 d-flex align-items-center">
            <div className="card-body ">
              <h5 className="card-title"><i>Hi I'M </i> <span className="home-title">Muzammil Hussasin</span>  <br />
                FRONT END DEVELOPER
              </h5>
              <p className="card-text mt-2">
                Lorem ipsum dolor sit amet consectetur adipisicing<br /> elit.  Amet, commodi earum dolorum dicta incidunt consectetur?
              </p>
              <button className="contact"><a className="nav-link Links " href="#contact">
                  Contact
                </a></button>
              <button className='contact mx-2'>Learn More</button>
            </div>
          </div>
          <div className="col-md-4">
            <img src={Image} className="img-fluid rounded-start mt-4 hover-effect" alt="home"/>
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
