import React from 'react'
import cal from './Images/cal.png'
import glass from './Images/glass.png'

const Research = () => {
  return (
    <>
    <h3 className='my-5 text-center' id='research'>Research And Publication</h3>
      <div className=" bg-transparent p-5 " id="home">
        <div className="row g-0 ">
        <div className="col-md-6">
            <img src={glass} className="img-fluid rounded-start mt-4" alt="Glasses" />
          </div>
          <div className="col-md-6 d-flex align-items-center ">
            <div className="card-body p-4 shadow">
                <h5 className='card-title'>Optimal approach of three dimensional microstructure reconstructions and visualizations.</h5>
              <p className="card-text ">
              The research article, Optimal approach of three dimensional microstructure reconstructions and visualizations has been featured on the front cover of the issue (June 2013: Volume 3, Number 2) in the journal Materials express as highlights.
              </p>
            </div>
          </div>
          
        </div>
      </div>
      <div className="mb-5 bg-transparent px-5 " id="home">
        <div className="row g-0 ">
        
          <div className="col-md-6 d-flex align-items-center ">
            <div className="card-body p-4 shadow">
                <h5 className='card-title'>Curvature-driven 3D grain growth equation.</h5>
              <p className="card-text ">
              Currently, working towards derivation of a new curvature-driven 3D grain growth equation by taking into consideration both the geometrical properties and the topological changes associated with the grains, which can better describe the dynamics of 3D grain growth in space-filling polycrystalline structures. So far, the proposed grain growth models are either purely topological or geometrical.
              </p>
            </div>
          </div>
          <div className="col-md-6">
            <img src={cal} className="img-fluid rounded-start" alt="calculator" />
          </div>
          
        </div>
      </div>
    </>
  )
}

export default Research;
